:root {
  --brand-color: #4f2ee8;

  --accent-color: #e7e9fc;

  --white-color: #ffffff;

  --white-color: #fff;

  --support-ukraine: radial-gradient(
    183.6% 183.01% at 59.76% -23.42%,
    #4f2ee8 18.03%,
    #fff 100%
  );
}

p,
h1,
h2,
h3 {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

.support__container {
  width: 335px;
  height: 352px;
  padding: 24px 40px 20px 40px;
  background: var(--support-ukraine);
  box-shadow: 0px 10px 10px rgba(245, 245, 247, 0.1);
  border-radius: 16px;
}

.support__header-container {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 40px;
}

.support__header-title {
  text-transform: none;
  color: var(--white-color);
  font-size: 28px;
  letter-spacing: -0.04em;
}

.support__icon {
  stroke: none;
  margin-left: 12px;
  fill: var(--white-color);
}

.support__link {
  display: flex;
  align-items: center;
  height: 32px;
}

.support__number {
  margin-right: 16px;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.04em;
  color: var(--white-color);
  list-style: none;
}

.support__img {
  display: block;
  max-width: 100%;
  height: auto;
  filter: grayscale(100%) brightness(0) invert(100%);
  transition: filter 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.support__link:hover .support__img,
.support__link:focus .support__img {
  filter: none;
  transform-origin: center;
}

.support__btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: none;
  border-radius: 50%;
  background-color: var(--white-color);
  width: 32px;
  height: 32px;
  cursor: pointer;
}

.support__btn:hover {
  background-color: var(--brand-color);
}

.support__btn:hover .support__btn-icon {
  fill: var(--accent-color);
}

.support__btn-icon {
  width: 20px;
  height: 20px;
  transform: translateY(2px);
  fill: var(--brand-color);
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.swiper {
  width: 100%;
  height: 188px;
  margin-bottom: 16px;
}

.swiper-slide {
  height: 32px;
}

@media screen and (min-width: 768px) {
  .support__container {
    width: 335px;
    height: 474px;
    padding-bottom: 14px;
  }

  .swiper {
    margin-bottom: 24px;
    height: 292px;
  }

  .support__btn {
    width: 38px;
    height: 38px;
  }

  .support__btn-icon {
    width: 22px;
    height: 22px;
  }
}

@media screen and (min-width: 1440px) {
  .support__container {
    width: 356px;
    height: 474px;
    padding-bottom: 24px;
  }
}
