.black-theme body {
  background-color: rgba(32, 32, 36, 1);
}
.black-theme .header-container {
  background-color: black;
}
.black-theme .header-container {
  border-color: rgba(243, 243, 243, 1);
}
.black-theme .header-bookshelf,
.black-theme .nav-link-icon {
  fill: rgba(243, 243, 243, 1);
}
.black-theme .nav-link {
  color: rgba(243, 243, 243, 1);
}
.black-theme .modal {
  background-color: black;
  border-color: white;
}
.black-theme .modal-icon {
  stroke: white;
}
.black-theme .input-style {
  border-color: white;
}
.black-theme .input-style::placeholder {
  color: white;
}
.black-theme .form-btn-sign {
  background-color: white;
  color: black;
}
.black-theme .form-btn {
  color: rgba(246, 246, 246, 0.5);
}
.black-theme .form-btn.first {
  color: #4f2ee8;
}
.black-theme .form-icon {
  stroke: white;
  fill: black;
}
.black-theme .modal-icon-open {
  stroke: rgba(243, 243, 243, 1);
}
.black-theme .modal-icon-close {
  stroke: rgba(243, 243, 243, 1);
}
.black-theme .modal-desc-text {
  color: rgba(243, 243, 243, 1);
}
.black-theme .all-categories {
  color: rgba(255, 255, 255, 0.6);
}
.black-theme .all-categories::-webkit-scrollbar-thumb {
  background-color: rgba(17, 17, 17, 0.6); /* color of the scroll thumb */
  /* roundness of the scroll thumb */
  /* creates padding around scroll thumb */
}

.black-theme .all-categories-title {
  color: rgba(255, 255, 255, 0.6);
}
.black-theme .category:hover {
  color: rgba(234, 198, 69, 1);
}
.black-theme .chosen-category {
  color: rgba(234, 198, 69, 1);
}
.black-theme .backdrop {
  background-color: rgba(17, 17, 17, 0.1);
}

.black-theme .all-categories-title:hover {
  color: rgba(234, 198, 69, 1);
}
.black-theme .best-books-btn {
  color: rgba(17, 17, 17, 1);
}
.black-theme #amazon {
  filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%)
    contrast(80%);
}
.black-theme .home-title {
  color: rgba(255, 255, 255, 1);
}
.black-theme .modal-book-title {
  color: rgba(255, 255, 255, 1);
}
.black-theme .modal-book-description {
  color: rgba(255, 255, 255, 1);
}
.black-theme .modal-btn {
  color: rgba(255, 255, 255, 1);
}
.black-theme .group-book-title {
  color: rgba(255, 255, 255, 1);
}
.black-theme .shopping-list-js-book {
  background-color: black;
}
.black-theme .best-book-title {
  color: rgba(255, 255, 255, 1);
}
.black-theme .modal-window {
  background-color: rgba(17, 17, 17, 1);
  border: white;
}
.black-theme .best-books-btn {
  background-color: black;
  color: white;
}

.black-theme .modal-close-icon {
  stroke: rgba(255, 255, 255, 1);
}
.btnUp {
  position: fixed;
  right: 2px;
  top: 50%;
  width: 64px;
  height: 64px;
  transform: translate(-50%);
  cursor: pointer;

  border: 5px solid rgb(201, 192, 246);
  border-radius: 50%;
  background-color: rgba(79, 46, 232, 1);
  display: none;
}

.show {
  display: block;
}
.black-theme .shopping-list-default-message,
.black-theme .shopping-list-title-book,
.black-theme .shopping-list-title,
.black-theme .shopping-list-description {
  color: rgba(255, 255, 255, 1);
}
.black-theme .shopping-list-links-icon-amazon {
  filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%)
    contrast(80%);
}
