@font-face {
  font-family: DMSans-Variable;
  src: url("DMSans-VariableFont_opsz_wght.cac90019.ttf") format("truetype");
  font-weight: 400 700;
  font-style: normal;
}

@font-face {
  font-family: DMSans-Italic-Variable;
  src: url("DMSans-Italic-VariableFont_opsz_wght.9f863b5a.ttf") format("truetype");
  font-weight: 400;
  font-style: italic;
}

body {
  letter-spacing: -.02em;
  color: var(--black-color);
  background-color: var(--body-color);
  font-family: DMSans-Variable, sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.29;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

:root {
  --body-color: #f6f6f6;
  --white-color: #fff;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.is-hidden {
  display: none;
}

.visually-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.header {
  width: 100%;
  z-index: 3;
  margin-bottom: 40px;
  top: 0;
}

.header-container {
  z-index: 100;
  width: 100%;
  height: 64px;
  min-width: 375px;
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.header-logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-bookshelf {
  fill: #000;
  margin-left: 8px;
}

.nav {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-list {
  align-items: center;
  gap: 8px;
  display: flex;
}

.nav-items a {
  color: inherit;
  letter-spacing: -.04em;
  border-radius: 36px;
  align-items: center;
  padding: 8px 16px;
  font-family: DM Sans Medium, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28571;
  display: flex;
}

.nav-item a:hover, .nav-item a:focus, .nav-item a.active-page {
  background-color: #eac645;
  border-color: #eac645;
  font-weight: 700;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.nav-link-icon {
  margin-left: 4px;
}

.modal-icon-open, .modal-icon-close {
  stroke: #111;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  line-height: 0;
}

.header-btn-modal-open {
  stroke: #111;
  fill: #111;
  color: #000;
  cursor: pointer;
  height: 28px;
  background: none;
  border: none;
  padding: 0;
}

.toggle-switch {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 auto;
  display: flex;
}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#dcdcdc 0%, #4f2ee8 100%);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 20px;
  width: 20px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 2px;
  left: 2px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

input:checked + .slider {
  background: linear-gradient(#686868 0%, #4f2ee8 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + .slider:before {
  transform: translateX(16px);
}

.slider, .circle {
  border-radius: 24px;
}

.circle:before {
  border-radius: 50%;
}

input:checked + .circle:before {
  background-color: #fff;
}

.arrow-right {
  stroke: #eac645;
}

.sign-up {
  width: 184px;
  height: 48px;
  letter-spacing: -.04em;
  color: #fff;
  stroke: #eac645;
  background: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 12px 14px;
  font-family: DM Sans Medium, sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.sign-up:hover, .sign-up:focus {
  color: #eac645;
  border-color: #eac645;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.sign-in-up-ig {
  margin-left: 24px;
  display: inline-flex;
  position: relative;
}

.nav {
  display: none;
}

@media screen and (max-width: 768px) {
  .sign-up {
    display: none;
  }

  .visually-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
  }
}

@media screen and (min-width: 768px) and (max-width: 1440px) {
  .header-container {
    max-width: 768px;
    height: 72px;
    padding: 14px 32px;
  }

  .header {
    position: relative;
  }

  .sign-up {
    width: 184px;
    height: 45px;
    letter-spacing: -.04em;
    color: #fff;
    stroke: #eac645;
    background: #4f2ee8;
    border-radius: 36px;
    justify-content: space-between;
    align-items: center;
    padding: 12px 14px;
    font-weight: 500;
    line-height: 18px;
    text-decoration: none;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
  }

  .header-btn-modal-close, .header-btn-modal-open {
    display: none;
  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    max-width: 1440px;
    height: 72px;
    padding: 14px 24px;
  }

  .sign-up {
    width: 184px;
    height: 45px;
    letter-spacing: -.04em;
    color: #fff;
    stroke: #eac645;
    background: #4f2ee8;
    border-radius: 36px;
    justify-content: space-between;
    align-items: center;
    padding: 12px 14px;
    font-weight: 500;
    line-height: 18px;
    text-decoration: none;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
  }

  .nav-list {
    gap: 24px;
  }

  .nav {
    margin-left: 76px;
  }

  .header-btn-modal-close, .header-btn-modal-open {
    display: none;
  }
}

.header-modal {
  max-width: 375px;
  height: 100%;
  z-index: 50;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 375px) {
  .header-modal {
    width: 100%;
  }
}

.header-modal.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.disable-scroll {
  overflow: hidden;
}

.header-btn-modal-open.is-close {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.header-btn-modal-close {
  cursor: pointer;
  height: 28px;
  width: 28px;
  background: none;
  border: none;
  padding: 0;
  position: absolute;
  top: 17px;
  right: 20px;
}

.header-modal-container {
  margin-top: 104px;
  margin-bottom: 126px;
}

.modal-sign-up {
  width: 184px;
  height: 48px;
  letter-spacing: -.56px;
  color: #fff;
  background: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 12px 14px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.286;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.modal-sign-up:hover, .modal-sign-up:focus {
  color: #eac645;
  border-color: #eac645;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .header-modal {
    display: none;
  }

  .nav {
    margin-left: 24px;
    display: flex;
  }
}

.header-modal-img {
  z-index: -1;
  position: absolute;
  bottom: 0;
  left: 0;
}

.modal-nav {
  width: 100%;
  display: flex;
}

.modal-nav-list {
  flex-direction: column;
  gap: 16px;
  margin: 0 auto;
  display: flex;
}

.modal-nav-items {
  display: inline-block;
}

.modal-nav-link {
  color: #fff;
  letter-spacing: -.56px;
  border-radius: 36px;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.286;
  display: flex;
}

.modal-nav-item a:hover, .modal-nav-item a:focus {
  color: #111;
  background-color: #eac645;
  border-color: #eac645;
  padding: 8px 16px;
  font-weight: 700;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-nav-item a.active-page {
  color: #111;
  background-color: #eac645;
  border-color: #eac645;
  padding: 8px 16px;
  font-weight: 700;
}

.modal-nav-link-icon {
  fill: #fff;
  margin-left: 4px;
}

.modal-out {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.modal-log-out {
  letter-spacing: -.56px;
  width: 118px;
  height: 48px;
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 14px 24px;
  font-weight: 500;
  line-height: 1.286;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.modal-log-out:hover, .modal-log-out:focus {
  color: #eac645;
  background: #4f2ee8;
  border-color: #eac645;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
